<template>
  <div>
    <!--  -->
    <h2></h2>
    <div class="marquee-wrap">
      <ul class="marquee-list" :class="{'animate-up': animateUp}">
        <li class="darkRoom">
          <div class="two"></div>
          <div class="two"></div>
          <div class="two"></div>
          <div class="two"></div>
        </li>
        <li v-for="(item, index) in listData" :key="index" :class="{'lightRoom': item.id === 1, 'darkRoom': item.id === 0}">
          <div class="one">{{ item.time }}</div>
          <div class="one">{{ item.name }}</div>
          <div class="one">{{ item.type }}</div>
          <div class="one">{{ item.place }}</div></li>
      </ul>
    </div>
  </div>
</template>

<script>
import {getreportinginforecent12data} from "@/api/url";

export default {
  name: "RealtimepreparationChart",
  data() {
    return {
      animateUp: false,
      boots:[],
      listData:  [],
      timer: null,
      count:0,
      lasttime:'2001-09-05T16:00:00.000+00:00',
      control:false,
      place:null,
      name:null,
      id:0,
    }
  },
  mounted() {
    this.scrollAnimate();
    this.timer = setInterval(this.scrollAnimate, 100000);

  },
  methods: {
    async scrollAnimate() {
      const res = await getreportinginforecent12data({});
      // const res = [{"insert_time_":"2004-09-05T16:00:00.000+00:00","method":"","from_port_":"111","vessel_name_":"111"},
      //   {"insert_time_":"2004-09-05T16:00:00.000+00:00","method":"","from_port_":"111","vessel_name_":"222"},
      //   {"insert_time_":"2004-09-05T16:00:00.000+00:00","method":"","from_port_":"111","vessel_name_":"333"},
      //   {"insert_time_":"2004-09-05T16:00:00.000+00:00","method":"","from_port_":"111","vessel_name_":"[\"444\",\"444\"]"},
      //   {"insert_time_":"2004-09-05T16:00:00.000+00:00","method":"","from_port_":"111","vessel_name_":"555"},
      //   {"insert_time_":"2004-09-05T16:00:00.000+00:00","method":"","from_port_":"111","vessel_name_":"666"},
      //   {"insert_time_":"2004-09-05T16:00:00.000+00:00","method":"","from_port_":"111","vessel_name_":"777"},
      //   {"insert_time_":"2004-09-05T16:00:00.000+00:00","method":"","from_port_":"111","vessel_name_":"888"},];


      //
      for (let i = 6; i >= 0; i--) {
        const date = new Date(res[i].insert_time_);

        if (new Date(this.lasttime) < date) {
          if (res[i].method_ === "") {
            this.place = res[i].from_port_;
          } else {
            this.place = res[i].to_port_;
          }

          this.name = res[i].vessel_name_;

          if (this.name.includes('[') && this.name.includes(']')) {
            // 
            try {
              const arr = JSON.parse(this.name);
              if (Array.isArray(arr) && arr.length > 0) {
                // 
                this.name= arr[0];
              }
            } catch (error) {
              console.error('', error);
            }
          }


          if(this.id === 1){
            this.id = 0
          }
          else{
            this.id = 1
          }
          let boot = {
            time: date.getDate().toString().padStart(2, "0")+""+date.getHours().toString().padStart(2, "0")+"" ,
            type: res[i].method_,
            place: this.place,
            name: this.name,
            id:this.id,
          };

          //mounted()css.animate-up
          await new Promise((resolve) => setTimeout(resolve, 1500));
          this.animateUp = true;
          this.listData.unshift(boot);

          //+1
          if (this.listData.length > 7) {
            this.listData.pop();
          }
          this.animateUp = false;
        }
      }
      this.lasttime = res[0].insert_time_;
    }

  },
  destroyed() {
    clearInterval(this.timer)
  }
};

</script>

