<template>
  <div>

    <div class="chart2" ref="chartColumn"></div>
  </div>

</template>


<script>
import * as echarts from "echarts";

import {getboatmanagelistdata,} from "@/api/url";



export default {
  name: "AgeChart",
  data() {
    return {
      chartColumn: null,
      allData: null,


    }
  },
  methods: {

    initChart() {
      this.chartColumn = echarts.init(this.$refs.chartColumn)
      const option= {
        legend: {
          top: "73%",
          itemWidth: 10,
          itemHeight: 10,
          textStyle: {
            color: "rgba(255,255,255,.5)",
            fontSize: "9"
          }
        },
        tooltip: {
          backgroundColor:'rgba(255,255,255,0.8)',
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"

        },

        color: [
          "#006cff",
          "#60cda0",
          "#ed8884",
          "#ff9f7f",
          "#0096ff",
          "#9fe6b8",
          "#32c5e9",
          "#1d9dff"
        ],
        series: [
          {
            // mysql
            data: [{"name": "25", "value": 126}, {"name": "25-30", "value": 1143}, {
              "name": "30-35",
              "value": 3866
            }, {"name": "35-40", "value": 5352}, {"name": "40-45", "value": 7468}, {
              "name": "45-50",
              "value": 9684
            }, {"name": "50-55", "value": 11075}],
            name: "",
            type: "pie",

            radius: ["10%", "70%"],
            center: ["50%", "42%"],
            roseType: "radius",

            //  label
            label: {
              show:false,
              ellipsis: false,
              fontSize: 10,


            },
            // 
            labelLine: {
              // 
              length: 10,
              // 
              length2: 10
            },
            labelLayout: {
              hideOverlap: false
            }
          }
        ]
      };
      this.chartColumn.setOption(option)
    },
    getshuju(){
      getboatmanagelistdata({}).then((res)=>{
        this.alldata=res;
        this.updateChart();
      })
    },
    updateChart(){
      const upoption={
        series: {
          data: this.alldata,
        },
      }
      this.chartColumn.setOption(upoption);
    }

  },
  mounted: function () {
    var that = this;
    that.getshuju();
    setInterval(function(){
      that.getshuju()
    },10000000)
    this.initChart();
  }
}
</script>
<style scoped>
.chart2{
  margin-top: -10%;


}
</style>
